import * as Type from './actionType';
import axios from 'axios';


const detailContent = (data) => ({
	type: Type.ADD_DETAIL_CONTENT,
	data
})

export const getDetailContent = (id) => {
	return (dispatch) => {
		axios.get('/jianshu/api/detail.json?id=' + id).then((res) => {
			dispatch(detailContent(res.data.data))
		}).catch((res) => {
			console.log(`报错时间${new Date()}, 报错内容 ${res}`)
		})
	}
}