import styled from 'styled-components';

export const DetailWraper = styled.div `
	overflow: hidden;
	width: 620px;
	margin: 0 auto;
	padding-bottom: 100px;
`
export const Header = styled.div `
	margin: 50px 0 20px 0;
	line-height: 44px;
	font-size: 34px;
	color: #333;
	font-weight: bold;
`

export const Content = styled.div `
	color: #666;
	img {
		width: 100%;
	}
	p {
		margin: 20px 0;
		font-size: 16px;
		line-height: 30px;
	}
	b {
		font-weight: bold;
	}
`