import styled from 'styled-components';

export const HomeWraper = styled.div `
	width: 960px;
	margin: 0 auto ;
	height: 300px;
`

export const HomeLeft = styled.div `
	overflow: hidden;
	margin-left: 15px;
	padding-top: 30px;
	width: 625px;
	float: left;
	.banner_img {
		width: 625px;
		height: 270px;
	}
`

export const HomeRight = styled.div `
	width: 280px;
	float: right;
`

export const Topicwraper = styled.div `
	overflow: hidden;
	padding: 20px 0 10px 0;
	margin-left: -18px;
	border-bottom: 1px solid #dcdcdc;
`

export const TopicItem = styled.div `
	float: left;
	height: 32px;
	line-height: 32px;
	padding-right: 10px;
	margin-left: 18px;
	margin-bottom: 18px;
	font-size: 14px;
	color: #000000;
	background-color: #f7f7f7;
	border: 1px solid #dcdcdc;
	border-radius: 4px;
	.topic_pic {
		float: left;
		width: 32px;
		height: 32px;
		margin-right: 10px;
	}
`

// List
export const ListItem = styled.div `
	padding: 20px 0;
	border-bottom: 1px solid #dcdcdc;
	.pic {
		float: right;
		width: 125px;
		height: 100px;
		border-radius: 10px;
	}
`

export const ListInfo = styled.div `
	float: left;
	width: 500px;
	.title {
		line-height: 27px;
		font-size: 18px;
		font-weight: bold;
		color: #333;
	}
	.des {
		line-height: 24px;
		font-size: 13px;
		color: #999;
	}
`

export const Recomendwraper = styled.div `
	margin-bottom: 20px;
	margin-top: 20px;
	width: 280px;
`
export const RecomendItem = styled.div `
	width: 280px;
	height: 50px;
	background: url(${(props)=> props.imgItem});
	background-size: contain;
`

export const LoadMore = styled.div `
	width: 100%;
	height: 40px;
	margin: 30px 0;
	line-height: 40px;
	background-color: #a5a5a5;
	text-align: center;
	border-radius: 20px;
	color: #fff;
`

export const BackTop = styled.div `
	position: fixed;
	bottom: 30px;
	right: 50px;
	width: 60px;
	height: 60px;
	line-height: 60px;
	text-align: center;
	border: 1px solid #ccc;
`

export const WriterWraper = styled.div `
	width: 100%;
	height: 200px;
	line-height: 200px;
	text-align: center;
	font-size: 20px;
	box-shadow: 0 0 8px rgba(0,0,0,.2);
`