import * as Type from './actionType';
import axios from 'axios';
import {
	fromJS
} from 'immutable'

const changeHomelist = (data) => ({
	type: Type.GET_HOME_DATA,
	data
})

const addHomeList = (data, nextPage) => ({
	type: Type.ADD_ARTICLE_LIST,
	data: fromJS(data),
	nextPage
})

export const changeScrollBool = (bool) => ({
	type: Type.CHANGE_SCROLL_TOP,
	bool
})

export const getHomeList = () => {
	return (dispatch) => {
		axios.get('/jianshu/api/home.json').then((res) => {
			const data = res.data.data;
			dispatch(changeHomelist(data))
		}).catch((res) => {
			console.log(`报错时间${new Date()}, 报错内容 ${res}`)
		})
	}
}

export const getMoreList = (page) => {
	return (dispatch) => {
		axios.get('/jianshu/api/homeList.json?page=' + page).then((res) => {
			const data = res.data.data;
			dispatch(addHomeList(data, page + 1))
		}).catch((res) => {
			console.log(`报错时间${new Date()}, 报错内容 ${res}`)
		})
	}
}