import React, {
	PureComponent
} from 'react';
import {
	Topicwraper,
	TopicItem
} from '../style'
import {
	connect
} from 'react-redux'

class Topic extends PureComponent {
	render() {
		const {
			topicList
		} = this.props
		return (
			<Topicwraper>
				{topicList.map((item)=>{
					return <TopicItem key={item}>
						<img 
						alt=""
						className="topic_pic" 
						src={item.get('imgUrl')}/>
						{item.get('title')}
					</TopicItem>
				})}
				
			</Topicwraper>
		)
	}
}

const mapStateToProps = (state) => ({
	topicList: state.getIn(['home', 'topicList'])
})

export default connect(mapStateToProps, null)(Topic)