import React, {
	PureComponent
} from 'react';
import {
	Recomendwraper,
	RecomendItem
} from '../style'
import {
	connect
} from 'react-redux';

class Recomend extends PureComponent {
	render() {
		const {
			List
		} = this.props;
		return (
			<Recomendwraper>
				{
					List.map((item)=>{
						return <RecomendItem key={item} imgItem={item.get('imgUrl')}></RecomendItem>
					})
				}
			</Recomendwraper>
		)
	}
}
const mapStateToProps = (state) => ({
	List: state.getIn(['home', 'recommendList'])
})

export default connect(mapStateToProps, null)(Recomend)