import {
	fromJS
} from 'immutable';
import * as Type from './actionType';

const defaultState = fromJS({
	login: false
})

export default (state = defaultState, action) => {
	switch (action.type) {
		case Type.CHANGE_LOGIN:
			return state.set('login', action.value)
		case Type.LOGOUT:
			return state.set('login', action.value)
		default:
			return state
	}
}