import {
	fromJS
} from 'immutable';
import * as Type from './actionType';

const defaultState = fromJS({
	title: "shui",
	content: ""
})

export default (state = defaultState, action) => {
	switch (action.type) {
		case Type.ADD_DETAIL_CONTENT:
			return (
				// state.merge({ ...state
				// } = fromJS(action.data)),
				state.merge({
					title: fromJS(action.data.title),
					content: fromJS(action.data.content)
				})
			)
		default:
			return state
	}
}