import styled from 'styled-components';
import logoPic from '../../statics/logo.png'
export const HeaderWrapper = styled.div `
	position: relative;
	height: 58px;
	border-bottom: 1px solid #f0f0f0;
	z-index: 1;
`

export const Logo = styled.div `
	position: absolute;
	top: 0;
	left: 0;
	width: 100px;
	height: 56px;
	background: url(${logoPic});
	background-size: contain;
`

export const Nav = styled.div `
	width: 960px;
	height: 100%;
	padding-right: 70px;
	box-sizing: border-box;
	margin: 0 auto 0;
`

export const NavItem = styled.div `
	line-height: 56px;
	padding: 0 15px;
	font-size: 17px;
	color: #333;
	&.left {
		float:left;
	}
	&.right {
		float:right;
		color: #969696;
	}
	&.active {
		color: #ea6f5a;
	}
`

export const NavSearch = styled.input.attrs({
	placeholder: '搜索'
})
`
	width: 160px;
	height: 38px;
	padding: 0 35px 0 20px;
	margin-top: 9px;
	margin-left: 20px;
	box-sizing: border-box;
	outline: none;
	border: none;
	border-radius: 19px;
	background-color: #eee;
	font-size14px;
	color: #666;
	&::placeholder {
		color: #999;
	}
	&.focused {
		width: 240px;
	}
	&.slide-enter {
		width: 160px;
		transition: all .2s ease-out;
	}
	&.slide-enter-active {
		width: 240px;
	}
	&.slide-exit {
		width: 240px;
		transition: all .2s ease-out;
	}
	&.slide-exit-active {
		width: 160px;
	}
`
export const SearchWrapper = styled.div `
	position: relative;
	float: left;
	.zoom {
		position: absolute;
		right: 5px;
		bottom: 5px;
		width: 30px;
		height: 30px;
		line-height: 30px;
		text-align: center;
		border-radius: 15px;
		&.focused  {
			background-color: #aaa
		}
	}
	.refresh {
		float: right;
		font-size12px;
		margin-right: 5px;
		transition: all 0.2s ease-out;
		transform-origin: center center;
	}
`

export const SearchInfo = styled.div `
	position: absolute;
	left: 0;
	top: 56px;
	width: 240px;
	padding: 0 20px;
	box-shadow: 0 0 8px rgba(0,0,0,.2);
	background-color: #fff;
`

export const SearchInfoTitle = styled.div `
	margin-top: 20px;
	margin-bottom: 15px;
	line-height: 20px;
	font-size: 12px;
	color: #969696;
`

export const SearchInfoSwitch = styled.span `
	float: right;
	font-size: 12px;
`

export const SearchInfoList = styled.div `
	overflow: hidden;
`

export const SearchInfoItem = styled.a `
	display: block;
	float: left;
	margin-right: 10px;
	margin-bottom: 10px;
	line-height: 20px;
	padding: 0 5px;
	font-size: 12px;
	border: 1px solid #999;
	border-radius: 2px;
	cursor: pointer;
	color: #787878;
`

export const Addition = styled.div `
	position: absolute;
	right: 0;
	top: 0;
	height: 56px;
`

export const Button = styled.div `
	&.reg {
		color: #ec6149
	}
	&.writting {
		color: #fff;
		background-color: #ec6149
	}
	float: right;
	margin-top: 9px;
	margin-right: 15px;
	padding: 0 20px;
	line-height: 38px;
	border-radius: 19px;
	border: 1px solid #ec6149;
	font-size: 14px;
`