import React, {
	PureComponent
} from 'react';
import {
	HomeWraper,
	HomeLeft,
	HomeRight,
	BackTop
} from './style'
import Topic from './components/Topic'
import Writer from './components/Writer'
import Recomend from './components/Recomend'
import List from './components/List'
import {
	connect
} from 'react-redux'
import {
	actionCreate
} from './store';


class Home extends PureComponent {

	handleTop() {
		window.scrollTo(0, 0)
	}
	render() {
		return (
			<HomeWraper>
				<HomeLeft>
					<img className="banner_img" alt="" src="//upload.jianshu.io/admin_banners/web_images/4660/224da83c76e01d5deff07e163615921233af5c82.jpg?imageMogr2/auto-orient/strip|imageView2/1/w/1250/h/540"/>
					<Topic />
					<List />
				</HomeLeft>
				<HomeRight>
					<Recomend/>
					<Writer/>
				</HomeRight>
				{this.props.backTop ? <BackTop onClick={this.handleTop}>Top</BackTop>: null}
			</HomeWraper>
		)
	}
	componentDidMount() {
		this.props.changeHomeList();
		this.bindEvents();
	}
	bindEvents() {
		window.addEventListener('scroll', this.props.changeScroll)
	}
	componentWillUnmount() {
		window.removeEventListener('scroll', this.props.changeScroll)
	}
}
const mapState = (state) => ({
	backTop: state.getIn(['home', 'backTop'])
})
const mapDispatch = (dispatch) => ({
	changeHomeList() {
		dispatch(actionCreate.getHomeList())
	},
	changeScroll() {
		if (document.documentElement.scrollTop >= 400) {
			dispatch(actionCreate.changeScrollBool(true))
		} else {
			dispatch(actionCreate.changeScrollBool(false))
		}
	}
})

export default connect(mapState, mapDispatch)(Home)