import React, {
	Component
} from 'react';
import {
	HeaderWrapper,
	Logo,
	Nav,
	NavItem,
	NavSearch,
	SearchInfo,
	SearchInfoTitle,
	SearchInfoSwitch,
	SearchInfoItem,
	SearchInfoList,
	Addition,
	Button,
	SearchWrapper
} from './style'
import {
	CSSTransition
} from 'react-transition-group';
import {
	connect
} from 'react-redux';
import {
	actionCreate
} from './store';
import {
	actionCreate as loginActionCreate
} from '../../pages/login/store'
import {
	Link
} from 'react-router-dom'

class Header extends Component {
	getListArea() {
		const {
			focused,
			list,
			page,
			mouseIn,
			totalPage,
			handleMouseEnter,
			handleMouseLeave,
			handleChangePage
		} = this.props;
		const newList = list.toJS()
		const pageList = []
		if (newList.length) {
			for (let i = page * 10; i < (page + 1) * 10; i++) {
				pageList.push(
					<SearchInfoItem key={newList[i]} >{newList[i]}</SearchInfoItem>
				)
			}
		}
		if (focused || mouseIn) {
			return <SearchInfo 
				onMouseEnter = {handleMouseEnter}
				onMouseLeave = {handleMouseLeave}
			>
				<SearchInfoTitle>
					热门搜索
					<SearchInfoSwitch onClick = {()=>{handleChangePage(page, totalPage, this.spinIcon)}}>换一换</SearchInfoSwitch>
					<i ref={(icon)=>{this.spinIcon = icon}} className="iconfont refresh">&#xe6a7;</i>
				</SearchInfoTitle>
				<SearchInfoList>
					{pageList}
				</SearchInfoList>
			</SearchInfo>
		} else {
			return null
		}
	}
	render() {
		const {
			focused,
			login,
			handleInputFocus,
			handleInputBlur,
			logout
		} = this.props
		return (
			<HeaderWrapper>
				<Link to="/">
					<Logo/>
				</Link>
				<Nav>
					<NavItem className = "left active">首页</NavItem>
					<NavItem className = "left">下载APP</NavItem>
					{login?
						<NavItem onClick={logout} className = "right">退出</NavItem>
						:<Link to="/login"><NavItem className = "right">登录</NavItem></Link>}
					<NavItem className = "right">
						<i className="iconfont">&#xe636;</i>
					</NavItem>
					<SearchWrapper>
						<CSSTransition 
							in = {focused}
							timeout = {300}
							classNames = "slide"
						>
							<NavSearch
								className = {focused ? 'focused': ""}
								onFocus = {handleInputFocus}
								onBlur = {handleInputBlur}
							></NavSearch>
						</CSSTransition>
						<i className={focused ? 'focused iconfont zoom': 'iconfont zoom'}>&#xe637;</i>
						{this.getListArea()}
					</SearchWrapper>
				</Nav>
				<Addition>
					<Link to="/write"><Button className="writting"><i className="iconfont">&#xe61b;</i>写文章</Button></Link>
					<Button className="reg">注册</Button>
				</Addition>
			</HeaderWrapper>
		)
	}
}



const mapStateToProps = (state) => ({
	// focused: state.header.focused
	// focused: state.header.get('focused'),
	focused: state.getIn(['header', 'focused']),
	list: state.getIn(['header', 'list']),
	page: state.getIn(['header', 'page']),
	mouseIn: state.getIn(['header', 'mouseIn']),
	totalPage: state.getIn(['header', 'totalPage']),
	login: state.getIn(['login', 'login'])
})
const mapDispatchToProps = (dispatch) => ({
	handleInputFocus() {
		dispatch(actionCreate.getList())
		dispatch(actionCreate.searchFoucs());
	},
	handleInputBlur() {
		dispatch(actionCreate.searchBlur());
	},
	handleMouseEnter() {
		dispatch(actionCreate.mouseEnter())
	},
	handleMouseLeave() {
		dispatch(actionCreate.mouseLeave())
	},
	handleChangePage(page, totalPage, spin) {
		let originAngle = spin.style.transform.replace(/[^0-9]/ig, '');
		if (originAngle) {
			originAngle = parseInt(originAngle, 10);
		} else {
			originAngle = 0
		}
		spin.style.transform = 'rotate(' + (originAngle + 360) + 'deg)'
		if (page < totalPage) {
			dispatch(actionCreate.changePage(page + 1))
		} else {
			dispatch(actionCreate.changePage(0))
		}
	},
	logout() {
		dispatch(loginActionCreate.logout())
	}
})

export default connect(mapStateToProps, mapDispatchToProps)(Header);