import * as Type from './actionType';
import axios from 'axios';
import { fromJS } from 'immutable';
const changelist = (data)=> ({
	type: Type.CHANGE_LIST,
	data: fromJS(data),
	totalPage: Math.floor(data.length / 10 )
})

export const searchFoucs = () => ({
	type: Type.SEARCH_FOCUS
});
export const searchBlur = () => ({
	type: Type.SEARCH_BLUR
});
export const mouseEnter  = () => ({
	type: Type.MOUSE_ENTER
});
export const mouseLeave  = () => ({
	type: Type.MOUSE_LEAVE
});
export const changePage = (page)=> ({
	type: Type.CHANGE_PAGE,
	page
})
export const getList = () => {
	return (dispatch)=>{
		axios.get('/jianshu/api/headerList.json').then((res)=> {
			const data = res.data;
			dispatch(changelist(data.data))
		}).catch((res)=>{
			console.log(`报错时间${new Date()}, 报错内容 ${res}`)
		})
	}
}